import styles from '../pages.module.css';
import { useState } from 'react';
import { Button, EmailInput } from '@ya.praktikum/react-developer-burger-ui-components';
import { Link, Navigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { performForgotPassword } from '../../../store/actions/accountActions';

function ForgotPassword() {
  const [email, setEmail] = useState('');
  const dispatch = useDispatch();
  const emailSent = useSelector((state) => state.accountDataStore.resetPassword.emailSent);
  const isAuthenticated = useSelector((state) => state.accountDataStore.isAuthenticated);

  function handleEmailChange(e) {
    setEmail(e.target.value);
  };

  function handleForgotPassword() {
    dispatch(performForgotPassword(email));
  };

  return (
    <div className={`${styles.entrance}`}>
      {isAuthenticated && (
        <Navigate to="/" replace={true} />
      )}
      {emailSent && (
        <Navigate to="/reset-password" replace={true} />
      )}
      <div className={`${styles.form}`}>
        <p className="text text_type_main-medium mb-6" style={{ textAlign: 'center' }}> </p>
        <EmailInput
          onChange={handleEmailChange}
          value={email}
          placeholder=" e-mail"
          name="email"
          extraClass="mb-6"
        />
        <Button
          onClick={handleForgotPassword}
          htmlType="button"
          type="primary"
          size="medium"
          extraClass={`mb-20 ${styles.button}`}>
          
        </Button>
        <p className="text text_type_main-default text_color_inactive mb-4" style={{ textAlign: 'center' }}>
           ? <Link to="/login" className={styles.link}></Link>
        </p>
      </div>
    </div>
  );
};

export default ForgotPassword;
