import { CheckMarkIcon } from '@ya.praktikum/react-developer-burger-ui-components';
import { useSelector } from 'react-redux';
import Preloader from '../../Preloader/Preloader';

function OrderDetails() {
  const orderData = useSelector((store) => store.orderDataStore);

  //        
  if (Object.keys(orderData).length === 0) {
    return <Preloader />;
  }

  return (
    <div>
      <p className="text text_type_digits-large mt-30 mb-8">{orderData.order.number}</p>
      <p className="text text_type_main-medium mb-15"> </p>
      <CheckMarkIcon type="primary" />
      <p className="text text_type_main-default mt-15 mb-2">   </p>
      <p className="text text_type_main-default text_color_inactive mb-30">    </p>
    </div>
  );
}

export default OrderDetails;
