import styles from '../pages.module.css';
import { Button, PasswordInput, EmailInput } from '@ya.praktikum/react-developer-burger-ui-components';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, Navigate } from 'react-router-dom';
import { performLogin } from '../../../store/actions/accountActions';

function Login() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const dispatch = useDispatch();
  const loginError = useSelector((state) => state.accountDataStore.loginError);
  const isAuthenticated = useSelector((state) => state.accountDataStore.isAuthenticated);

  function handlePasswordChange(e) {
    setPassword(e.target.value);
  };

  function handleEmailChange(e) {
    setEmail(e.target.value);
  };

  function handleLogin() {
    dispatch(performLogin({ email, password }));
  };

  return (
    <div className={`${styles.entrance}`}>
      {/*    ,    "/" */}
      {isAuthenticated && (
        <Navigate to='/' replace={true} />
      )}
      <div className={`${styles.form}`}>
        <p className="text text_type_main-medium mb-6" style={{ textAlign: 'center' }}></p>
        <EmailInput
          onChange={handleEmailChange}
          value={email}
          name="email"
          extraClass="mb-6"
        />
        <PasswordInput
          onChange={handlePasswordChange}
          value={password}
          name="password"
          icon="ShowIcon"
          extraClass="mb-6"
        />
        {loginError && <div className="text text_type_main-default text_color_error mb-6"> : {loginError}</div>}
        <Button
          onClick={handleLogin}
          htmlType="button"
          type="primary"
          size="medium"
          extraClass={`mb-20 ${styles.button}`}>
          
        </Button>
        <p className="text text_type_main-default text_color_inactive mb-4" style={{ textAlign: 'center' }}>
           -  ? <Link to="/register" className={styles.link}></Link>
        </p>
        <p className="text text_type_main-default text_color_inactive" style={{ textAlign: 'center' }}>
           ? <Link to="/forgot-password" className={styles.link}> </Link>
        </p>
      </div>
    </div>
  );
};

export default Login;

