import { LOAD_INGREDIENTS, INCREASE_INGREDIENTS_COUNT, DECREASE_INGREDIENTS_COUNT } from '../../utils/constants';
const ingredientsList = [];

//  reducerIngredientsList     
//      -  (ingredientsList)  / (action)
// ..         action
//       type  responseJSON.data (  )
function ingredientsListReducer(state = ingredientsList, action) {
  switch (action.type) {
    case LOAD_INGREDIENTS:
      return action.ingredients;
    case INCREASE_INGREDIENTS_COUNT:
      //     state(   )  map    
      return state.map((ingredient) => {
        // action.ingredientID -   action "actionIncreaseCount"       
        // dispatch(actionIncreaseCount(draggableIngredientInfo._id)) -  "draggableIngredientInfo._id"   
        // .    ,        id        id
        //            count    count   +1
        if (ingredient._id === action.ingredientID) {
          if (ingredient.type === 'bun') {
            return { ...ingredient, count: ingredient.count + 2 };
          } else {
            return { ...ingredient, count: ingredient.count + 1 };
          }
        }
        //    
        return ingredient;
      });
    case DECREASE_INGREDIENTS_COUNT:
      //     state(   )  map    
      return state.map((ingredient) => {
        // action.ingredientID -   action "actionDecreaseCount"       
        // dispatch(actionDecreaseCount(actionDicreaseCount(el._id))) -  "el._id"   
        // .    ,        id        id
        //            count    count   -1,    
        if (ingredient._id === action.ingredientID) {
          if (ingredient.type === 'bun') {
            return { ...ingredient, count: 0 };
          } else {
            return { ...ingredient, count: ingredient.count - 1 };
          }
        }
        //    
        return ingredient;
      });
    default:
      return state;
  }
};

export { ingredientsListReducer };
